    padding-left: 15px;
    border-radius: 12px;
    font-size: 14px;
  }
}



.header{
  height: 45px;position: relative;
  h1{
    &.sgis{
      height: 45px;
      width: 90px;
      background: #1b5850;
      a{
        margin: 7px 0 0 7px;
        text-align: center;
