      }
    }
  }
  .container{
    position: absolute;left: 90px;right: 0;top: 0;
    height: 45px;background-color: #20655a;
  }
  &-left{
    float: left;
    overflow: hidden;
    .tit{
      position: relative;
      float: left;
      height: 45px;
      line-height: 45px;
      padding: 0 10px;
      margin-right: 10px;
      color:#fff;
