#aside{
  position: fixed;left: 0;top: 45px;bottom: 0;z-index: 2;
  .snb{
    width: 90px;height: 100%;background: #fff;
  }
}


.gnb{
  &-li{
    position: relative;
    border-top: 1px solid #e1e1e2;
    &:first-child{
      border-top: none;
    }
    &:after{content:"";display: block;position: absolute;bottom: 0;left: 10px;right: 10px;height: 1px;background-color: #fff;opacity: 0.15;}
    &:hover{
       background: #3ca96e;
      > a{
        color: #fff;
      }
    }
    &.active{
      background: #3ca96e;
      padding-bottom: 10px;
      > a{
        color: #fff;
      }
      ul{
        display: block;
      }
    }
    > a{
      color: #7c8284;
      display: block;background-repeat: no-repeat;height: 100px;background-position: 50% 15px;
      padding-top: 60px;font-size: 14px;font-weight: 700;line-height: 1.1;text-align: center;
    }
    &-1{
      >a{
        background-image: url("../images/gnb-1.png");
        
        
      }
      &:hover,
      &.active{
        >a{background-image: url("../images/gnb-1-on.png");}
      }
    }
    &-2{
      >a{
        background-image: url("../images/gnb-2.png");
        
      }
      &:hover,
      &.active{
        >a{background-image: url("../images/gnb-2-on.png");}
      }
    }
    &-3{
      >a{
        background-image: url("../images/gnb-3.png");
      }
      &:hover,
      &.active{
        > a{background-image: url("../images/gnb-3-on.png");}
      }
    }

    ul{
      display: none;
      li{
        text-align: center;
        +li{
          padding-top: 5px;
        }
        a{
          display: inline-block;width: 80px;background-color: #fff;color: #7c8284;border-radius:15px;
          font-size: 13px;line-height: 25px;height: 25px;text-align: center;letter-spacing: -0.05em;
          &:hover,
          &.active{background-color: #ff9600;color: #fff;}
        }
      }
    }
  }
}

.since{
  border-top: 1px solid #e1e1e2;
  background-color: #fff;overflow: hidden;text-align: center;
  &-tit{
    color: #7c8284;font-size: 13px;font-weight: 700;height: 40px;line-height: 40px;
  }
  &-button{
    border-bottom: 1px solid #e1e1e2;
    a{
      display: block;color: #7c8284;font-size: 13px;color: #7c8284;height: 35px;line-height: 35px;
      &.active{
        background-color: #2fa07c;color: #fff;
      }
    }
  }
}

