.menu-wrap{
  .tab{
    overflow: hidden;
    padding: 10px 10px 0;
    background: #f8f8f8;
    border-bottom: 1px solid #e5e5e5;
    li{
      float: left;
      width: 82px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 14px;
      background: #e5e5e5;
      border-radius:5px 5px 0 0;
      cursor:pointer;
      &.active{
        background: #3ca96e;
        color:#fff;
      }
      & + li{
        margin-left: 3px;
      }
    }
  }
}
.sub-sort{
  padding: 10px 0 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid #e5e5e5;
  .lbl-radio{
    display: inline-block;
    vertical-align: top;
    & + .lbl-radio{
      margin-left: 12px;
    }
  }
  input[type="radio"]{
    overflow: hidden;
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: 0;
    border: 0;
    clip:rect(0,0,0,0);
    & + label{
      display: inline-block;
      position: relative;
      height: 18px;
      padding-left: 25px;
      cursor: pointer;
      vertical-align: top;
      &:before{
        content: '';
        position: absolute;
        top: 0px;
        left: 0;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url('../images/bg-radio.png');
        text-align: center;
        box-sizing: border-box;
      }
    }
    &:checked{
      & + label{
        &:after{
          content: '';
          position: absolute;
          top: 0px;
          left: 0;
          width: 18px;
          height: 18px;
          background-repeat: no-repeat;
          background-position: center;
          background-image: url('../images/bg-radio-on.png');
        }
      }
    }    
  }
}
.menu-total{
  margin-bottom: 10px;
  font-size: 18px; 
  color: #222;
  letter-spacing: -0.05em;
}
.menu-group{
  border: 1px solid #d8d8d8;
  border-radius: 5px;
  background-color: #fff;
  margin: 10px 10px 0 10px;
  &.active{
    border: 2px solid #19ce60;
    -webkit-box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.2);
    .menu-toggle{
      background-image: url('../images/ic-arrow-up.png');
      font-weight: 700;
      color: #19ce60;
    }
  }
  .menu-toggle{
    display: block;
    padding: 8px 10px;
    background-repeat: no-repeat;
    background-position: right 10px center;
    background-image: url('../images/ic-arrow-down.png');
    background-size: 12px;
    font-size: 13px;
    color: #878c8f;
  }
  .menu-list{
    overflow-y: auto;
    display: none;
    height: 200px;
    margin-bottom: 10px;
    > a{
      display: block;
      position: relative;
      padding: 8px 15px 8px 15px;
      background-color: #fff;
      font-size: 13px;
      line-height: 1.2em;
      color: #666;
      -webkit-transition: 0.2s ease;
      transition: 0.2s ease;
      &:hover{
        background-color: #d1f5df;
        color: #222;
        font-weight: 700;
        &:before{
          background-color: #222;
        }
      }
      &:before{
        display: block;
        content: '';
        clear: both;
        position: absolute;
        top: 15px;
        left: 9px;
        width: 3px;
        height: 3px;
        background-color: #666;
      }
    }
  }
}

