.tab-wrap{
  width: 100%;
  margin-bottom: 20px;
}
.tab-menu{
  &:after{
    display: block;
    content: '';
    clear: both;
  }
  li{
    display: inline-block;
    position: relative;
    background-color: #e5e5e5;
    vertical-align: top;
    &:first-child{
      border-radius: 3px 0 0 3px;
    }
    &:last-child{
      border-radius: 0 3px 3px 0;
    }
    & + li{
      margin-left: -4px;
      > a{
        position: relative;
        &:before{
          display: block;
          content: '';
          clear: both;
          position: absolute;
          top: 10px;
          left: 0;
          width: 1px;
          height: 20px;
          background-color: #bebebe;
        }        
      }
    }
    &.on{
      &:after{
        display: block;
        content: '';
        position: absolute;
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        z-index: 1;
        background-color: #222;
        border-radius: 3px;        
      }
      > a{
        &:before{
          display: none;
        }
        color: #fff;
      }
      & + li{
        > a{
          &:before{
            display: none;
          }
        }
      }
    }
    > a{
      display: block;
      position: relative;
      z-index: 2;
      padding: 7px 19.9px 9px;
      font-size: 16px;
      color: #222;
    }
  }
}
.tab-con{
  display: none;
}

.slider-conbox{
  width: 100%;
  margin-top: 10px;
  > strong{
    display: inline-block;
    width: 55px;
    font-size: 17px;
    font-weight: 700;
    color: #222;
    vertical-align: top;
  }
  .slider-wrap{
    display: inline-block;
    width: 100%;
    margin-top: 7px;
    vertical-align: top;
  }
  .slider{
    position: relative;
    width: 100%;
    height: 10px;
    border: 1px solid #e5e5e5;
    border-radius: 10px;
    .bar{
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      height: 8px;
      border-radius: 10px;
      background-repeat: no-repeat;
      background-position: 0 center;
    }
    .dot{
      display: inline-block;
      position: relative;
      position: absolute;
      top: -40px;
      left: 50%;
      transform: translateX(-50%);
      padding: 4px 15px;
      background-color: #666666;
      border-radius: 10px;
      font-size: 15px;
      color: #fff;
      vertical-align: top;
      &:before{
        display: block;
        content: '';
        clear: both;
        position: absolute;
        top: 100%;
        left: 50%;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        width: 10px;
        height: 5px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-image: url('../images/bg-graph-dot.gif');
      }
      &:after{
        display: block;
        content: '';
        clear: both;
        position: absolute;
        bottom: -30px;
        left: 50%;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 1px solid #e5e5e5;
        background-color: #fff;
        -webkit-box-shadow: 0px 3px 0px 0px rgba(0, 0, 0, 0.07);
        box-shadow: 0px 3px 0px 0px rgba(0, 0, 0, 0.07);
      }
    }
  }
  .slider-legend{
    margin-top: 5px;
    &:after{
      display: block;
      content: '';
      clear: both;
      span{
        display: inline-block;
        font-size: 15px;
        color: #666;
        vertical-align: middle;
      }
    }
  }
}

.time-graph-pop{
  opacity: 0;
  visibility: hidden;
  position: absolute;
  bottom: 10px;
  left: 370px;
  right: 467px;
  z-index: 2;
  /* width: calc(100% - 120px); */
  height: 65px;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 10px;
  &.active{
    opacity: 1;
    visibility: inherit;
  }
  .control{
    display: inline-block;
    width: 45px;
    height: 45px;
    padding: 12px 0;
    background-color: #3ca96e;
    border-radius: 50%;
    color: #fff;
    font-weight: 400;
    background-position: center;
    text-align: center;
    vertical-align: top;
    background-image: url("../images/time-graph_play.png");
    background-repeat: no-repeat;
    &.stop{
      background-image: url("../images/time-graph_stop.png");
    }
  }
  .time-graph-wrap{
    display: inline-block;
    width: calc(100% - 170px);
    height: 16px;
    margin-top: 5px;
    margin-left: 20px;
    vertical-align: top;
    .time-graph{
      position: relative;
      width: 100%;
      height: 100%;
      background: #c4e5d3;
      border-radius: 10px;
      ul{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 16px;
        border-radius: 10px;
        > li{
          position: relative;
          float: left;
          width: 12.50%;
          height: 100%;
          &:before{
            display: block;
            content: '';
            clear: both;
            position: absolute;
            top: 3px;
            left: 3px;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #fff;
          }
          &:last-child{
            border-radius: 0 10px 10px 0;
            &:after{
              display: block;
              content: '';
              clear: both;
              position: absolute;
              top: 3px;
              left: auto;
              right: 3px;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              background-color: #fff;
            }
          }
          &:first-child{
            border-radius: 10px 0 0 10px
          }
        }
      }
    }
    .bar{
      position: absolute;
      top: 0;
      left: 0;
      width: calc((100% / 9) * 4 + 15px);
      height: 16px;
      background-color: #3ca96e;
      border-radius: 10px;

    }
    .time-graph-legend{
      width: 100%;
      margin-top: 5px;
      ul{
        width: 100%;
        &:after{
          display: block;
          content: '';
          clear: both;
        }
        > li{
          float: left;
          width: 12.70%;
          font-size: 15px;
          text-align: center;
          color: #666;
          &:first-child{
            width: 5.55%;
            text-align: left;
          }
          &:last-child{
            width: 5.55%;
            text-align: right;
          }
        }
      }
    }
  }
  .btn-down{
    position: absolute;
    top: -1px;
    right: -1px;
    display: inline-block;
    width: 66px;
    height: 65px;
    background-color: #fafafa;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url('../images/history_down.png');
    border-radius: 0 10px 10px 0;
    border: 1px solid #e5e5e5;
  }
}

