.comparison-panel{
    width: 100%;
    .flex_wrap{
        display: flex;
        border-bottom: 1px solid #e5e5e5;
        margin-bottom: 15px;
        &>div{
            .slider-conbox{
              border: none;
              margin-bottom: 0;
            }
            width: 50%;
            &.left{
                padding-right: 15px;
            }
            &.right{
                padding-left: 15px;
            }
        }              
    }
    .comparison-item{
        background: #fff;
        &:last-child{
            border-bottom: none;
        }
        &.comparison-item01{
          &:first-child{
            border-radius: 10px 10px 0 0;
          }
          .flex-box{
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: center;
            align-content: stretch;
            margin-bottom: 15px;
            .slider-conbox{
              width: 65%;
            }
            .line-01{
              width: 35%;
            }
          }
          .slider-conbox{
            .slider-wrap{
              .full-left,
              .full-right{
                color:#72787b;
                font-size: 14px;
              }
              .slider{
                width: 80%;
              }
              .dot{
                padding: 0;
                top: -15px;
                cursor:pointer;
                &:before{
                  display: none;
                }
              }
            }
          }
        }
        &.comparison-item02{
          
        }
        &.comparison-item03{
          &:first-child{
            border-radius: 10px 10px 0 0;
          }
          .flex-box{
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: center;
            align-content: stretch;
            margin-bottom: 15px;
            margin-top: 30px;
          }
          .slider-conbox{
            .slider-wrap{
              .full-left,
              .full-right{
                color:#72787b;
                font-size: 14px;
              }
              .slider{
                width: 70%;
              }
              .dot{
                cursor:pointer;
                width: 39px;
                height: 23px;
                font-size: 12px;
                padding: 0;
                text-align: center;
                line-height: 23px;
                &.dot01{
                  background: #27c988;
                  &:before{
                    background-image: url("../images/bg-graph-dot01.png")
                  }
                }
                &.dot02{
                  background: #ffa626;
                  &:before{
                    background-image: url("../images/bg-graph-dot02.png")
                  }
                }
                
              }
            }
          }
        }
        &.comparison-item04{
          
        }
        border-bottom: 1px solid #e5e5e5;
        padding: 0 10px 10px 10px;
    }
    .comparison-header{
        h3{
            border-bottom: 1px solid #e5e5e5;
            height: 35px;
            line-height: 35px;
            font-size: 14px;
            margin-bottom: 10px;
            span{
              position: relative;
              float: right;
              color:#72787b;
              font-size: 12px;
              &:before{
                position: absolute;
                left: -5px;
                top: 12px;
                display: block;
                content:'';
                width: 1px;
                height: 12px;
                background: #c0c3c4;

              }
            }
        }
    }
    .comparison-body{
        .line-00{
            strong{
                position: relative;
                display: inline-block;
                padding-left: 10px;
                &:after{
                    position: absolute;
                    left: 0;
                    top: 8px;
                    display: block;
                    content:'';
                    width: 3px;
                    height: 3px;
                    border-radius:50%;
                    background: #222222;
                }
            }
        }
        .line-01{
            text-align: right;
            strong{
                display: block;
                margin-bottom: 10px;
                font-size: 18px;
                color:#ff0000;
                span{
                  display: inline-block;
                  margin-left: 3px;
                  font-size: 14px;
                  color:#000000;
                }
            }
            p{
                &.type-color01{
                     span{
                        color:#19ce60;
                    }
                }
                &.type-color02{
                     span{
                        color:#19ccce;
                    }
                }
                &.type-color04{
                     span{
                        color:#d01800;
                    }
                }
                font-size: 14px;
                span{
                    display: inline-block;
                    margin-left: 5px;

                }
            }
        }
        .slider-conbox{
            .slider-wrap{
                .slider{
                    &.type-color01{
                        .bar{
                            background: #04cf5c;
                        }
                    }
                    &.type-color02{
                        .bar{
                            background: -webkit-gradient(linear, left top, right top, color-stop(0, #2390af), color-stop(0.8, #1d637e));
                            background: -moz-linear-gradient(left, #2390af 0%, #1d637e 80%);
                            background: -webkit-linear-gradient(left,  #2390af 0%, #1d637e 80%);
                            background: -o-linear-gradient(left,  #2390af 0%, #1d637e 80%);
                            background: -ms-linear-gradient(left,  #2390af 0%, #1d637e 80%);
                            background: linear-gradient(left,  #2390af 0%, #1d637e 80%);
                        }
                    }
                    &.type-color04{
                        .bar{
                            background: #ff9600;
                        }
                    }
                    .bar{
                        background-color: transparent;
                        max-width: 100%;
                    }
                }
            }
        }
        .chart-conbox{
            margin-bottom: 10px;
            padding: 20px;
            border: 1px solid #e6e6e6;
            background: #f4f6f9;
            border-radius: 10px;
            &.type-color01{
                .color-item01{
                    dt{
                        color:#72787b;
                        font-size: 12px;
                    }
                    dd{
                        div{
                            background: #3ca96e;
                        }
                    }
                }
                .color-item02{
                    dt{
                      color:#72787b;
                      font-size: 12px;
                    }
                    dd{
                        div{
                            background: #b8c9d8;
                        }
                    }
                }
                .color-item03{
                  margin-bottom: 10px;
                    dt{
                      color:#72787b;
                      font-size: 12px;
                    }
                    dd{
                        div{
                            background: #ff9600;
                        }
                    }
                }
            }
            &.type-color02{
                .color-item01{
                    dt{
                        color:#238faf;
                    }
                    dd{
                        div{
                            background: #238faf;
                        }
                    }
                }
                .color-item02{
                    dd{
                        div{
                            background: #163147;
                        }
                    }
                }
            }
            &.type-color03{
                .color-item01{
                    dt{
                        color:#23af78;
                    }
                    dd{
                        div{
                            background: #23af78;
                        }
                    }
                }
                .color-item02{
                    dd{
                        div{
                            background: #d01800;
                        }
                    }
                }
            }
            dl{
                display: flex;    
                align-items: center;
                &:first-child{
                    margin-bottom: 10px;
                }
                dt{
                    width: 20%;
                    color:#666666;
                    font-size: 15px;
                }
                dd{
                    width: 80%;
                    display: flex;
                    justify-content: space-between;
                    div{
                        max-width: 260px;
                        height: 20px;
                        border-radius:5px;
                        $charttotal: 260;
                        @for $i from 1 through  $charttotal {
                          &.per-#{$i} {
                            width: 1px * $i;
                          }
                        }
                    }
                    span{
                        font-size: 13px;
                    }
                }
            }
        }
        .graph-box{
            position: relative;
            border: 1px solid #e5e5e5;
            border-radius: 10px;
            background: #f4f6f9;
            min-height: 100px;
            .legend_list{
                position: absolute;
                left: 10px;
                top: 10px;
                &.type-color01{
                    .color-item01{
                        span{background: #23af78;}
                    }
                    .color-item02{
                        span{background: #16473f;}
                    }
                }
                &.type-color02{
                    .color-item01{
                        span{background: #238faf;}
                    }
                    .color-item02{
                        span{background: #163147;}
                    }
                }
                &.type-color03{
                    .color-item01{
                        span{background: #23af78;}
                    }
                    .color-item02{
                        span{background: #d01800;}
                    }
                }
                li{
                    position: relative;
                    font-size: 13px;
                    padding-left: 30px;
                    margin-bottom: 5px;
                    span{
                        position: absolute;
                        left: 0;
                        top: 6px;
                        width: 20px;
                        height: 4px;
                        background: #000;
                    }
                }
            }
        }
    }
}


