.modal{
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  z-index: 999999;
  position: fixed;
  display: none;
  &-container{
    text-align: center;
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
    overflow-x: hidden;
    overflow-y: auto;
  }
  &-header{
    background-color: #e5e5e5;
    height: 40px;
    line-height: 40px;
    position: relative;
    text-align: left;
    border-radius: 5px 5px 0 0;
    padding-left: 10px;
    h2{
      background:url('../images/modal-blit.png') 0 50% no-repeat;
      font-size: 14px;font-weight: 800;padding-left: 12px;color: #000;
      em{
        font-size: 13px;font-weight: 700;
        font-style: normal;color: #666666;
      }
    }
    .close{
      position: absolute;
      right: 13px;
      top: 13px;
      width: 10px;height: 11px;
      display: block;
      background:url('../images/modal-close.png') 0 0 no-repeat;
    }
  }
  &-body{
        position: relative;
      display: inline-block;
      vertical-align: middle;
      margin: 0 auto;
      text-align: left;
      z-index: 505;
      width: 100%;
      border-radius:0 0 5px 5px;overflow: hidden;
      border:1px solid #e5e5e5
  }
  &-content{
      background: #fff;
      padding-top: 0;
      text-align: left;
      position: relative;
      padding:10px;
  }
  &-footer{
    border-top:1px solid #e5e5e5;
    padding:10px 0;
    text-align: center;
    background-color: #fff;font-size: 0;
    button{
      display: inline-block;font-size: 14px;font-weight: 700;
      border-radius: 5px;width: 83px;height: 30px;
    }
    .btn_1{background-color: #3ca96e;color:#fff;}
    .btn_2{background-color: #e5e5e5;color:#7c8284;margin-left: 3px}
  }
}

.modal-bg{
